import { useState, useEffect, useCallback, useRef } from 'react';
import { useSolanaAuth } from '../../hooks/useSolanaAuth';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { detectSolanaWallets } from '../../utils/walletDetection';

export interface SolanaLoginButtonProps {
  onSuccess?: () => void;
  onError?: (error: Error) => void;
  className?: string;
  variant?: 'default' | 'outline';
  size?: 'sm' | 'md' | 'lg';
  disabled?: boolean;
  /**
   * Hide the button if no Solana wallets are detected.
   * When true (default), button renders nothing if no wallets are installed.
   * When false, button always renders (useful for showing "install wallet" prompts).
   * @default true
   */
  hideIfNoWallet?: boolean;
  /**
   * Solana wallet adapter context. Pass this from @solana/wallet-adapter-react's useWallet().
   * The button will handle connection and signing automatically for a one-click experience.
   */
  walletContext?: {
    publicKey: { toBase58: () => string } | null;
    signMessage: ((message: Uint8Array) => Promise<Uint8Array>) | null;
    connected: boolean;
    connecting: boolean;
    connect: () => Promise<void>;
    wallet: { adapter: { name: string } } | null;
    select: (walletName: string) => void;
    wallets: Array<{
      adapter: {
        name: string;
        icon: string;
        readyState: string;
      };
    }>;
  };
}

/**
 * Solana wallet login button with one-click authentication.
 *
 * Handles wallet connection and message signing automatically.
 * If wallet is already connected, signs immediately.
 * If not connected, connects first then auto-signs.
 */
export function SolanaLoginButton({
  onSuccess,
  onError,
  className = '',
  variant = 'default',
  size = 'md',
  disabled = false,
  hideIfNoWallet = true,
  walletContext,
}: SolanaLoginButtonProps) {
  const { requestChallenge, signIn, isLoading: isAuthLoading } = useSolanaAuth();
  const [showWalletSelector, setShowWalletSelector] = useState(false);
  const [pendingLogin, setPendingLogin] = useState(false);
  const [triggerConnect, setTriggerConnect] = useState(false);
  const isProcessingRef = useRef(false);

  // Detect wallets from browser (used when walletContext not provided)
  // Use lazy initializer to avoid re-detecting on every render
  const [browserHasWallets] = useState(() => detectSolanaWallets());

  const connected = walletContext?.connected ?? false;
  const connecting = walletContext?.connecting ?? false;
  const publicKey = walletContext?.publicKey;
  const signMessage = walletContext?.signMessage;
  const wallet = walletContext?.wallet;
  const wallets = walletContext?.wallets ?? [];

  // Get installed/ready wallets from wallet adapter context
  const installedWallets = wallets.filter(
    (w) => w.adapter.readyState === 'Installed' || w.adapter.readyState === 'Loadable'
  );

  // Determine if any wallets are available
  // If walletContext is provided, use its wallet list; otherwise use browser detection
  const hasWallets = walletContext ? installedWallets.length > 0 : browserHasWallets;

  // Execute the actual sign-in flow
  const executeSignIn = useCallback(async () => {
    if (isProcessingRef.current) return;
    if (!publicKey || !signMessage) {
      onError?.(new Error('Wallet not ready'));
      return;
    }

    isProcessingRef.current = true;
    try {
      const pubKeyString = publicKey.toBase58();

      // Request challenge from server
      const challenge = await requestChallenge(pubKeyString);

      // Sign the message with wallet
      const messageBytes = new TextEncoder().encode(challenge.message);
      const signatureBytes = await signMessage(messageBytes);

      // Validate signature
      if (!(signatureBytes instanceof Uint8Array) || signatureBytes.length === 0) {
        throw new Error('Wallet returned invalid signature');
      }

      // Encode signature
      let signature: string;
      try {
        signature = btoa(String.fromCharCode(...signatureBytes));
      } catch {
        throw new Error('Failed to encode signature');
      }

      // Authenticate with server
      await signIn(pubKeyString, signature, challenge.message);

      onSuccess?.();
    } catch (err) {
      const error = err instanceof Error ? err : new Error(String(err));
      onError?.(error);
    } finally {
      isProcessingRef.current = false;
      setPendingLogin(false);
    }
  }, [publicKey, signMessage, requestChallenge, signIn, onSuccess, onError]);

  // Auto-connect when wallet is selected and triggerConnect is set
  useEffect(() => {
    if (triggerConnect && wallet && !connected && !connecting && walletContext?.connect) {
      setTriggerConnect(false);
      walletContext.connect().catch((err) => {
        onError?.(err instanceof Error ? err : new Error(String(err)));
        setPendingLogin(false);
      });
    }
  }, [triggerConnect, wallet, connected, connecting, walletContext, onError]);

  // Auto-execute sign-in when connected with pending login
  // COMP-04: Catch unhandled rejections from executeSignIn (defensive - errors already
  // handled internally, but .catch() prevents unhandledrejection events)
  useEffect(() => {
    if (pendingLogin && connected && publicKey && signMessage && !isProcessingRef.current) {
      executeSignIn().catch(() => {
        /* Errors already passed to onError callback inside executeSignIn */
      });
    }
  }, [pendingLogin, connected, publicKey, signMessage, executeSignIn]);

  // Hide button if no wallets detected and hideIfNoWallet is true
  // Note: This must come after all hooks to satisfy React rules of hooks
  if (hideIfNoWallet && !hasWallets) {
    return null;
  }

  const handleClick = async () => {
    if (disabled || isAuthLoading || connecting) return;

    if (connected && publicKey && signMessage) {
      // Already connected - sign immediately
      setPendingLogin(true);
      await executeSignIn();
    } else if (wallet) {
      // Wallet selected but not connected - connect and queue login
      setPendingLogin(true);
      setTriggerConnect(true);
    } else if (installedWallets.length === 1) {
      // Only one wallet installed - auto-select it
      walletContext?.select(installedWallets[0].adapter.name);
      setPendingLogin(true);
      setTriggerConnect(true);
    } else if (installedWallets.length > 1) {
      // Multiple wallets - show selector
      setShowWalletSelector(true);
    } else {
      // No wallets - show error
      onError?.(
        new Error('No Solana wallet found. Please install Phantom or another Solana wallet.')
      );
    }
  };

  const handleSelectWallet = (walletName: string) => {
    setShowWalletSelector(false);
    walletContext?.select(walletName);
    setPendingLogin(true);
    setTriggerConnect(true);
  };

  const sizeClasses = {
    sm: 'cedros-button-sm',
    md: 'cedros-button-md',
    lg: 'cedros-button-lg',
  };

  const variantClasses = {
    default: 'cedros-button-solana',
    outline: 'cedros-button-solana-outline',
  };

  const isLoading = isAuthLoading || connecting || (pendingLogin && !connected);

  return (
    <>
      <button
        type="button"
        className={`cedros-button ${variantClasses[variant]} ${sizeClasses[size]} ${className}`}
        onClick={handleClick}
        disabled={disabled || isLoading}
        aria-label="Continue with Solana"
      >
        {isLoading ? (
          <LoadingSpinner size="sm" />
        ) : (
          <svg
            className="cedros-button-icon"
            width="18"
            height="18"
            viewBox="0 0 128 128"
            fill="currentColor"
            aria-hidden="true"
          >
            <path d="M25.38 96.04a4.35 4.35 0 0 1 3.07-1.27h91.68c1.93 0 2.9 2.34 1.54 3.7l-17.71 17.72a4.35 4.35 0 0 1-3.07 1.27H9.21c-1.93 0-2.9-2.34-1.54-3.7l17.71-17.72z" />
            <path d="M25.38 11.81a4.47 4.47 0 0 1 3.07-1.27h91.68c1.93 0 2.9 2.34 1.54 3.7L103.96 31.96a4.35 4.35 0 0 1-3.07 1.27H9.21c-1.93 0-2.9-2.34-1.54-3.7L25.38 11.81z" />
            <path d="M102.62 53.76a4.35 4.35 0 0 0-3.07-1.27H7.87c-1.93 0-2.9 2.34-1.54 3.7l17.71 17.72a4.35 4.35 0 0 0 3.07 1.27h91.68c1.93 0 2.9-2.34 1.54-3.7L102.62 53.76z" />
          </svg>
        )}
        <span>Continue with Solana</span>
      </button>

      {/* Wallet Selector Modal */}
      {showWalletSelector && (
        <div
          className="cedros-modal-backdrop"
          onClick={() => setShowWalletSelector(false)}
          role="presentation"
        >
          <div
            className="cedros-modal cedros-wallet-selector"
            role="dialog"
            aria-modal="true"
            aria-labelledby="wallet-selector-title"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="cedros-modal-header">
              <h2 id="wallet-selector-title" className="cedros-modal-title">
                Select Wallet
              </h2>
              <button
                type="button"
                className="cedros-modal-close"
                onClick={() => setShowWalletSelector(false)}
                aria-label="Close"
              >
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" aria-hidden="true">
                  <path
                    d="M18 6L6 18M6 6l12 12"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                  />
                </svg>
              </button>
            </div>
            <div className="cedros-modal-content">
              <div className="cedros-wallet-list">
                {installedWallets.map((w) => (
                  <button
                    key={w.adapter.name}
                    type="button"
                    className="cedros-wallet-option"
                    onClick={() => handleSelectWallet(w.adapter.name)}
                  >
                    <img
                      src={w.adapter.icon}
                      alt=""
                      width="32"
                      height="32"
                      className="cedros-wallet-icon"
                    />
                    <span>{w.adapter.name}</span>
                  </button>
                ))}
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
