import { useState, useCallback, useMemo } from 'react';
import { useCedrosLogin } from '../context/useCedrosLogin';
import { ApiClient, handleApiError } from '../utils/apiClient';
import { validateSolanaPublicKey } from '../utils/validation';
import type { AuthResponse, AuthError, ChallengeResponse } from '../types';

export interface UseSolanaAuthReturn {
  requestChallenge: (publicKey: string) => Promise<ChallengeResponse>;
  signIn: (publicKey: string, signature: string, message: string) => Promise<AuthResponse>;
  isLoading: boolean;
  error: AuthError | null;
  clearError: () => void;
}

/**
 * Hook for Solana wallet authentication.
 *
 * @example
 * ```tsx
 * function SolanaLogin() {
 *   const { requestChallenge, signIn, isLoading } = useSolanaAuth();
 *   const { publicKey, signMessage } = useWallet();
 *
 *   const handleLogin = async () => {
 *     const challenge = await requestChallenge(publicKey.toBase58());
 *     const signature = await signMessage(new TextEncoder().encode(challenge.message));
 *     const result = await signIn(
 *       publicKey.toBase58(),
 *       Buffer.from(signature).toString('base64'),
 *       challenge.message
 *     );
 *   };
 * }
 * ```
 */
export function useSolanaAuth(): UseSolanaAuthReturn {
  const { config, _internal } = useCedrosLogin();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<AuthError | null>(null);

  const apiClient = useMemo(
    () =>
      new ApiClient({
        baseUrl: config.serverUrl,
        timeoutMs: config.requestTimeout,
        retryAttempts: config.retryAttempts,
      }),
    [config.serverUrl, config.requestTimeout, config.retryAttempts]
  );

  const requestChallenge = useCallback(
    async (publicKey: string): Promise<ChallengeResponse> => {
      // Validate public key format before making API call
      if (!validateSolanaPublicKey(publicKey)) {
        const authError: AuthError = {
          code: 'INVALID_PUBLIC_KEY',
          message: 'Invalid Solana public key format',
        };
        setError(authError);
        throw authError;
      }

      setIsLoading(true);
      setError(null);

      try {
        const data = await apiClient.post<ChallengeResponse>(
          '/solana/challenge',
          { publicKey },
          { credentials: 'omit' }
        );
        return data;
      } catch (err) {
        const authError = handleApiError(err, 'Failed to get challenge');
        setError(authError);
        throw authError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const signIn = useCallback(
    async (publicKey: string, signature: string, message: string): Promise<AuthResponse> => {
      // Validate public key format before making API call
      if (!validateSolanaPublicKey(publicKey)) {
        const authError: AuthError = {
          code: 'INVALID_PUBLIC_KEY',
          message: 'Invalid Solana public key format',
        };
        setError(authError);
        throw authError;
      }

      setIsLoading(true);
      setError(null);

      try {
        const data = await apiClient.post<AuthResponse>('/solana', {
          publicKey,
          signature,
          message,
        });
        config.callbacks?.onLoginSuccess?.(data.user, 'solana');
        _internal?.handleLoginSuccess(data.user, data.tokens);
        return data;
      } catch (err) {
        const authError = handleApiError(err, 'Solana sign-in failed');
        setError(authError);
        throw authError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient, config.callbacks, _internal]
  );

  const clearError = useCallback(() => setError(null), []);

  return {
    requestChallenge,
    signIn,
    isLoading,
    error,
    clearError,
  };
}
